
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { notifyFake } from "@/fakebackend/notifyFake";
import { INotify } from "@/interfaces/INotify";

export default defineComponent({
  name: "cDevAlert",
  components: {},
  mixins: [NuiComponentMixin],
  data() {
    return {
      notifyList: [] as INotify[],
      notifId: 0,
    };
  },
  props: {
    name: {
      type: String,
    },
  },
  methods: {
    handleNUI({ action, data }: { action: string; data: unknown }) {
      if (action === "addNotification") {
        let notif = data as INotify;
        notif.id = this.notifId++;

        this.notifyList.push(notif);

        setTimeout(() => {
          const findNotify = this.notifyList.find((n) => n.id === notif.id);
          if (findNotify) {
            findNotify.isShowing = true;
          }
        }, 100);

        setTimeout(() => {
          const findNotify = this.notifyList.find((n) => n.id === notif.id);
          if (findNotify) {
            findNotify.isShowing = false;

            setTimeout(() => {
              this.notifyList = this.notifyList.filter((n) => n.id !== notif.id);
            }, 600);
          }
        }, notif.timeRemaining * 1000);
      }
    },
  },
  mounted: function () {
    /*setInterval(() => {
      if (this.active) {
        console.log("Nois aqui");
        console.log("Tamanho da Stirng: ", this.notifyList.length);
        if (this.notifyList.length > 0) {
          this.notifyList.forEach((notify, index) => {
            if (notify.isShowing && notify.timeRemaining > 0) {
              notify.timeRemaining = notify.timeRemaining - 1;
            } else {
              notify.isShowing = false;
              this.notifyList.splice(index, 1);
            }
          });
        } else {
          this.active = false;
        }
      }
    }, 1000);*/
  },
  //   unmounted: function () {
  //   },
  created() {
    //this.notifyList = notifyFake;
    //console.log("Created Notify List : ", this.notifyList.length);
  },
});
