
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";

export default defineComponent({
  name: "cDevPagination",
  components: {},
  mixins: [NuiComponentMixin],
  data() {
    return {};
  },
  props: {
    selectedPage: {
      type: Number,
    },
    maxPage: {
      type: Number,
    },
  },
  methods: {
    changePage(page: number) {
      this.$emit("changePage", Math.min(page, this.maxPage || 0));
    },
  },
});
