
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import { Alert } from "@/fakebackend/alertFake";
import { IAlert } from "@/interfaces/IAlert";

export default defineComponent({
  name: "cDevAlert",
  components: {},
  mixins: [NuiComponentMixin],
  data() {
    return {
      alert: Alert,
      active: false,
    };
  },
  props: {
    name: {
      type: String,
    },
    locale: {
      type: Function,
      required: true,
    }
  },
  methods: {
    handleNUI({ action, data }: { action: string; data: unknown }) {
      if (action === "setAlert") {
        const { type, title, desc } = data as IAlert;
        this.alert = { type, title, desc };
      } else if (action === "setVisible") {
        const { visible } = data as { visible: boolean };
        this.active = visible;
      }
    },
    handleKeyDown(e: KeyboardEvent) {
      if (!this.active) return;
      if (!this.alert) return;

      if (e.key === "Escape") {
        if (this.alert?.type === "success" || this.alert?.type === "delete") {
          this.postNUI("okay", {});
        } else {
          this.postNUI("cancel", {});
        }
      } else if (e.key === "Enter") {
        this.postNUI("okay", {});
      }
    },
    onOkay() {
      this.postNUI("okay", {});
    },
    onCancel() {
      this.postNUI("cancel", {});
    },
  },
  mounted: function () {
    window.addEventListener("keydown", this.handleKeyDown);
  },
  unmounted: function () {
    window.removeEventListener("keydown", this.handleKeyDown);
  },
});
