
import { defineComponent } from "vue";
import NuiComponentMixin from "@/mixins/NuiComponentMixin";
import {
  DefaultChannelChat,
  DefaultChatColor,
  DefaultSuggestionChat,
  ESuggestionMode,
  IChat,
  IChatChannel,
  ISuggestionChat,
} from "@/interfaces/IChat";
import { fakeChat } from "@/fakebackend/chatFake";
import inputTagging from "@/utils/inputTagging.vue";
export default defineComponent({
  components: { inputTagging },
  mixins: [NuiComponentMixin],
  data() {
    return {
      message: "",
      messages: [] as IChat[],
      channelsChat: DefaultChannelChat,
      selectedSuggestion: undefined as ISuggestionChat | undefined,
      suggestion: [] as ISuggestionChat[], //DefaultSuggestionChat,
      receiveNotify: false,
      activeNotify: false,
      activatedChat: false,
      colorsVariable: DefaultChatColor,
      mentionsIndicator: false,
      icons: {} as { [name: string]: string },

      displaySuggestions: [] as ISuggestionChat[],
      overParamIndex: -1,
      peekTimeout: null as number | null,
      newMessageCount: 0,
      config: {
        MessageHistoryLimit: 25,
        MessageSizeLimit: 1000,
      },
    };
  },
  props: {
    visible: Boolean,
    name: String,
  },
  methods: {
    handleNUI(data: any) {
      switch (data.action) {
        case "open":
          this.open();
          break;
        case "close":
          this.close();
          break;
        case "addMessage":
          this.addMessage(data.data.message, data.data.mentioned, data.data.dontNotify);
          break;
        case "addIcon":
          this.addIcon(data.data.name, data.data.data);
          break;
        case "updateChannels":
          this.updateChannels(data.data.channels);
          break;
        case "updateConfig":
          this.updateConfig(data.data.config);
          break;
        case "addSuggestion":
          this.addSuggestion(data.data.suggestion);
          break;
        case "updateTheme":
          this.updateTheme(data.data.theme);
          break;
      }
    },
    updateTheme(theme: any) {
      this.colorsVariable = theme;
    },
    addSuggestion(suggestion: ISuggestionChat) {
      this.suggestion = this.suggestion.filter((s) => s.title !== suggestion.title);
      this.suggestion.push(suggestion);
    },
    getMessageHistory() {
      return this.messages.slice(-this.config.MessageHistoryLimit);
    },
    updateConfig(config: any) {
      this.config = config;
    },
    updateChannels(channels: IChatChannel[]) {
      const selectedChannel = this.channelsChat.find((c) => c.selected);
      this.channelsChat = channels;

      if (selectedChannel) {
        const newSelectedChannel = this.channelsChat.find((c) => c.name === selectedChannel.name);
        if (newSelectedChannel) {
          newSelectedChannel.selected = true;
        }
      }
    },
    addIcon(name: string, data: any) {
      this.icons[name] = data;
    },
    handleKeyDown(event: any) {
      if (!this.activatedChat) return;

      switch (event.code) {
        case "Escape":
          this.requestClose();
          break;
        case "Tab":
          event.preventDefault();
          this.selectActiveSuggestion();

          break;
        case "ArrowUp":
          event.preventDefault();
          this.selectPreviousSuggestion();
          break;
        case "ArrowDown":
          event.preventDefault();
          this.selectNextSuggestion();
          break;
      }
    },
    selectPreviousSuggestion() {
      if (this.displaySuggestions.length === 0) return;

      const selectedSuggestion = this.displaySuggestions.find((s) => s.hovering);

      if (selectedSuggestion) {
        const index = this.displaySuggestions.indexOf(selectedSuggestion);
        if (index === 0) {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[this.displaySuggestions.length - 1].hovering = true;
        } else {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[index - 1].hovering = true;
        }
      } else {
        this.displaySuggestions[this.displaySuggestions.length - 1].hovering = true;
      }

      this.$nextTick(() => {
        const suggestionList = this.$refs.suggestionList as HTMLElement;
        const selectedSuggestion = suggestionList.querySelector(".hoveringSuggestion") as HTMLElement;
        if (selectedSuggestion) {
          selectedSuggestion.scrollIntoView();
        }
      });
    },
    selectNextSuggestion() {
      if (this.displaySuggestions.length === 0) return;

      const selectedSuggestion = this.displaySuggestions.find((s) => s.hovering);

      if (selectedSuggestion) {
        const index = this.displaySuggestions.indexOf(selectedSuggestion);
        if (index === this.displaySuggestions.length - 1) {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[0].hovering = true;
        } else {
          this.displaySuggestions[index].hovering = false;
          this.displaySuggestions[index + 1].hovering = true;
        }
      } else {
        this.displaySuggestions[0].hovering = true;
      }

      this.$nextTick(() => {
        const suggestionList = this.$refs.suggestionList as HTMLElement;
        const selectedSuggestion = suggestionList.querySelector(".hoveringSuggestion") as HTMLElement;
        if (selectedSuggestion) {
          selectedSuggestion.scrollIntoView();
        }
      });
    },
    requestClose() {
      this.postNUI("request-close", {});
    },
    changeSelectedChannel(channel: IChatChannel) {
      this.channelsChat.forEach((item: IChatChannel) => {
        item.selected = false;
        item.hovering = false;
      });

      const hoverT = Date.now();

      channel.selected = true;
      channel.hovering = true;
      channel.hoverT = hoverT;

      setTimeout(() => {
        if (channel.hoverT === hoverT) channel.hovering = false;
      }, 500);

      this.postNUI("select-channel", { channel: channel.name });
    },
    getSelectedChannel() {
      return this.channelsChat.find((item: IChatChannel) => item.selected);
    },
    cycleChannels() {
      const channels = this.channelsChat.filter((item: IChatChannel) => item.enabled);
      const getSelectedChannel = channels.find(
        (item: IChatChannel) => item.selected
      );
      if (getSelectedChannel) {
        const index = channels.indexOf(getSelectedChannel);
        if (index === channels.length - 1) {
          this.changeSelectedChannel(channels[0]);
        } else {
          this.changeSelectedChannel(channels[index + 1]);
        }
      } else {
        this.changeSelectedChannel(channels[0]);
      }
    },
    addMessage(message: IChat, mentioned: boolean, dontNotify?: boolean) {
      message.message = message.message.replaceAll("<mention", "<span class='mention'").replaceAll("</mention>", "</span>");

      this.messages.push(message);

      if (!this.activatedChat && !dontNotify) {
        if (mentioned) {
          this.mentionsIndicator = true;
        }

        this.newMessageCount++;

        this.peek();
      }

      this.$nextTick(() => {
        const chatBox = this.$refs.chatBox as HTMLElement;
        chatBox.scrollTop = chatBox.scrollHeight;
      });
    },
    getIconFromChannel(channel: string) {
      const icon = this.getChannelValues(channel)?.icon;
      return icon ? this.icons[icon] : "";
    },
    getChannelValues(name: string) {
      const findChannel = this.channelsChat.find(
        (item: IChatChannel) => item.name === name
      );
      if (findChannel) return findChannel;
    },
    updateMentions(term: string, mode: ESuggestionMode, caretPos?: number) {
      if (term.length === 0) {
        this.displaySuggestions = [];
        this.overParamIndex = -1;

        return;
      }

      const fullTerm = term;

      if (mode === ESuggestionMode.COMMAND) {
        const terms = term.split(" ");
        term = terms[0];

        if (
          terms.length > 1 &&
          !this.suggestion.some(
            (item) => item.title.toLowerCase() === `/${term}`
          )
        ) {
          this.displaySuggestions = [];
          this.overParamIndex = -1;

          return;
        }
      }

      this.displaySuggestions = this.suggestion.filter((item) => {
        return (
          item.title.toLowerCase().includes(term.toLowerCase()) &&
          item.mode === mode
        );
      });

      this.displaySuggestions.forEach((item) => {
        item.hovering = false;
      });

      const first = this.displaySuggestions[0];

      if (first) {
        first.hovering = true;
      }

      if (mode === ESuggestionMode.COMMAND) {
        let paramIndex = caretPos
          ? fullTerm.substring(0, caretPos).split(" ").length - 1
          : 0;
        paramIndex = paramIndex - 1;

        this.overParamIndex = paramIndex;
      }
    },
    setSelectedSuggestion(item: ISuggestionChat) {
      if (!item) return;

      const inputTagging = this.$refs.inputTagging as any;

      if (item.mode === ESuggestionMode.COMMAND) {
        inputTagging.addCommand(item.title);
      } else {
        inputTagging?.addMention(item.title, item.id);
      }

      this.displaySuggestions = [];
    },
    selectActiveSuggestion() {
      const first = this.displaySuggestions.find((s) => s.hovering);

      const inputTagging = this.$refs.inputTagging as any;

      if (first) {
        if (first.mode === ESuggestionMode.COMMAND) {
          inputTagging.addCommand(first.title);
        } else {
          inputTagging?.addMention(first.title, first.id);
        }

        this.displaySuggestions = [];
      } else {
        this.cycleChannels();
      }
    },

    // PEEK
    peek() {
      if (this.peekTimeout) clearTimeout(this.peekTimeout);

      this.receiveNotify = true;

      this.peekTimeout = setTimeout(() => {
        this.receiveNotify = false;
        this.peekTimeout = null;
      }, this.mentionsIndicator ? 2500 : 1500);
    },

    open() {
      this.activeNotify = true;
      this.activatedChat = true;
      this.displaySuggestions = [];
      this.mentionsIndicator = false;
      this.newMessageCount = 0;

      this.$nextTick(() => {
        const inputTagging = this.$refs.inputTagging as any;
        inputTagging.focusInput();

        const chatBox = this.$refs.chatBox as HTMLElement;
        chatBox.scrollTop = chatBox.scrollHeight;
      });
    },

    close() {
      this.activeNotify = false;
      this.activatedChat = false;
    },

    submitMessage(message: string, mentions: number[]) {
      this.postNUI("message", {
        message,
        mentions,
      });
    },

    handleMouseWheel(event: WheelEvent) {
      if (!this.activatedChat) return;

      const chatBox = this.$refs.chatBox as HTMLElement;

      if (event.deltaY > 0) {
        chatBox.scrollTop += 50;
      } else {
        chatBox.scrollTop -= 50;
      }
    },
  },
  mounted() {
    window.addEventListener("keydown", this.handleKeyDown);
    window.addEventListener("wheel", this.handleMouseWheel);
  },
  unmounted() {
    window.removeEventListener("keydown", this.handleKeyDown);
    window.removeEventListener("wheel", this.handleMouseWheel);
  },
  created: function () {
    //this.open();
    /*setTimeout(() => {
      this.newMessageCount++;
      this.peek();
    }, 500);

    setTimeout(() => {
      this.newMessageCount++;
      this.mentionsIndicator = true;
      this.peek();
    }, 2500);

    setTimeout(() => {
      this.open();

      setTimeout(() => {
        this.close();
      }, 1500);
    }, 5000);*/
  },
});
